<template>
  <div>
    <el-card class="box-card">
      <el-row>
        <b>星期：</b>
<el-input
 v-model="input"
  placeholder="请输入星期几"
  style="width: 150px;"
  ></el-input>
<el-button type="primary" @click="searchFun" icon="el-icon-search">搜索</el-button>
        <el-button type="primary" 
        @click="reset"
        icon="el-icon-magic-stick">重置</el-button>
      </el-row>
       <template>
  <el-table
    :data="tableData"
    border
    style="width: 100%"
    max-height="500"
    >
    <el-table-column
        prop="id"
        label="ID"
        >
      </el-table-column>
      <el-table-column
        prop="cname"
        label="课程">
      </el-table-column>
      <el-table-column
        prop="cno"
        label="课程编号">
      </el-table-column>
      <el-table-column
        prop="className"
        label="班级">
      </el-table-column>
      <el-table-column
        prop="cdate"
        label="星期">
      </el-table-column>
  </el-table>

  <div class="block">
    <el-pagination
     @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="pagesizes"
      :page-size="pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
 </template>
</el-card>
  </div>
</template>

<script>
import API from "../../utils/service"
export default {
  data() {
      return {
        name: '课程表',
        tableData: [],
        input: '',
        currentPage: 1,
        total: 0,
        pagesize: 5,
        pagesizes: [3,4,5,6],
        hashmap: {
          "1": "星期一",
          "2": "星期二",
          "3": "星期三",
          "4": "星期四",
          "5": "星期五",
          "6": "星期六",
          "7": "星期天",
        }
      }
  },
   mounted(){
    this.$bus.$emit('receiveParams',this.name)
    this.init()
    this.pages(1,this.pagesize)
  },
  methods: {
      init(){
      API({
        url:'/courseController/getCourse',
        method: 'get',
      }).then((res)=>{
        this.total = res.data.data.length
      })
    },
    pages(val,page) {
      // console.log('查询页码:',val,'页数:',page);
      API({
        url:`/courseController/getCourse/${val}/${page}`,
        method: 'get',
      }).then((response)=>{
        let table = response.data.data.records
        let maps = new Map([[1, "星期一"],[2, "星期二"],[3, "星期三"],[4, "星期四"],[5, "星期五"],[6,"星期六"],[7, "星期天"]])
        table = table.map(element=>{
          element.cdate = maps.get(element.cdate)
          return element
        })
       this.tableData = table
        })
    },
      handleCurrentChange(val) {
        console.log(val);
        this.pages(val,this.pagesize)
      },
       handleSizeChange(val) {
        this.pagesize = val
        this.pages(this.currentPage,this.pagesize)
      },
searchFun() {
  let maps = new Map([["一",1],["二",2],["三",3],["四",4],["五",5],["六",6],["天",7]])
      let cdate = maps.get(this.input)
      API({
        url:`/courseController/getCourse/1/${this.total}`,
        params: {
          cDate: cdate
        },
        method: 'get',
      }).then((response)=>{
        let table = response.data.data.records
        let maps = new Map([[1, "星期一"],[2, "星期二"],[3, "星期三"],[4, "星期四"],[5, "星期五"],[6,"星期六"],[7, "星期天"]])
        table = table.map(element=>{
          element.cdate = maps.get(element.cdate)
          return element
        })
       this.tableData = table
      })

    },
       reset() {
      this.init()
      this.pages(1,this.pagesize)
    },
  }
}
</script>

<style lang="scss" scoped>
.el-row{
    margin: 30px 0 50px 0;
  }
  .text {
    font-size: 20px;
  }

  // .item {
  //   padding: 18px 0;
  // }

  .box-card {
    width: 100%;
  }

  .el-card{
    padding: 0px;
  }

  .el-button--primary {
    background-color: #FAB6B6;
    border-color: #FAB6B6;
  }

  .el-button--primary:focus, .el-button--primary:hover {
    background: #e9baba;
    border-color: #f3dfdf;
    color: #FFF;
}

  ::v-deep .el-pagination.is-background .el-pager li:not(.disabled).active {
	background-color: #f3dfdf;
  }
</style>